import React, { useState, useEffect } from 'react';
import { auth } from './firebase';
import { signInWithEmailAndPassword, GoogleAuthProvider, signInWithPopup } from 'firebase/auth';
import { Link, useNavigate } from 'react-router-dom';

const Login = () => {
  const navigate = useNavigate();
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [error, setError] = useState('');
  
  useEffect(() => {
    const unsubscribe = auth.onAuthStateChanged( async (user) => {
      if (user) {
        navigate('/courses');
      }
    });
    return () => unsubscribe();
  }, [navigate]);

  const handleGoogleSignIn = async () => {
    const provider = new GoogleAuthProvider();
    try {
      const result = await signInWithPopup(auth, provider);
      const user = result.user;
      if (user) {
        navigate('/courses');
      }
    } catch (error) {
      setError('Google Sign-In failed');
    }
  };

  const fireLogin = (e) => {
    e.preventDefault();
    signInWithEmailAndPassword(auth, email, password)
    .then(() => {
        navigate('/courses');
    })
    .catch(() => {
        setError('Invalid email or password');
    });
  }

  const handleEmailChange = (e) => {
    setEmail(e.target.value);
  };

  const handlePasswordChange = (e) => {
    setPassword(e.target.value);
  };

  return (
    <div className='w-full min-h-[calc(100vh-65px)] max-h-fit bg-gray-100 text-white flex flex-col items-center justify-center'>
      <div className='py-[6vh]'>
        <div className='relative py-2 px-6'>
          <div className="absolute inset-0 bg-blue-400 -skew-y-6 skew-x-12" />
          <h1 className='text-6xl font-bold text-white p-3 relative z-10 tracking-wide'>Login</h1>
        </div>
      </div>

        <div className='flex flex-col items-center justify-center rounded-md shadow-2xl shadow-gray-500 w-full max-w-xl'>
          <button
            type='button'
            onClick={handleGoogleSignIn}
            className='bg-blue-400 text-white px-4 py-2 my-4 rounded-3xl duration-200 hover:bg-blue-500'
          >
            Sign in with Google
          </button>

          <div className='flex items-center w-full'>
            <hr className='flex-grow border border-gray-300'/>
            <span className='text-black mx-4'>Or</span>
            <hr className='flex-grow border border-gray-300'/>
          </div>

          <form onSubmit={fireLogin} className='mt-4 w-full max-w-sm'>
              <div>
              <label className='block text-black text-lg mb-2'>Email:</label>
              <input
                  type='text'
                  value={email}
                  onChange={handleEmailChange}
                  className='bg-gray-200 text-black w-full px-4 py-2 rounded-md'
                  placeholder='Enter email'
              />
              </div>

              <div className='mt-4'>
              <label className='block text-black text-lg mb-2'>Password:</label>
              <input
                  type='password'
                  value={password}
                  onChange={handlePasswordChange}
                  className='bg-gray-200 text-black w-full px-4 py-2 rounded-md'
                  placeholder='Enter password'
              />
              </div>

              {error && <p className='text-red-500 mt-2'>{error}</p>}

              <div className='w-full flex justify-center'>
              <button
                  type='submit'
                  className='bg-blue-400 text-white px-4 py-2 mt-4 rounded-3xl duration-200 hover:bg-blue-500'
              >
                  Login
              </button>
              </div>
          </form>

          <hr className='w-full border my-4 border-gray-300'/>

          <Link to='/register'>
            <button
              type='button'
              className='bg-gray-400 text-white px-4 py-2 mb-4 rounded-3xl duration-200 hover:bg-gray-500'
            >
              Don't Have An Account? Register
            </button>
          </Link>

        </div>
    </div>

  );
};


export { Login as default}